@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Slab:wght@900&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    height: 100%;
}

body {
    display: flex;
    justify-content: center;

    height: 100%;
}

.imgWithViewProject {

    width: 1440px;
    position: relative;
}

.imgLink {
    display: grid;
    grid-template-columns: repeat(2, 44%);
    justify-items: center;
    align-items: center;
    justify-content: center;
}


.h2 {
    position: absolute;
    left: 6.2rem;
    top: 9.5rem;
}

h2 {
    color: var(--text-text-buttun-primary, var(--color-text-text-button-primaty, #2E3346));
    font-family: 'Roboto';
    font-size: 4rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

a {
    position: relative;
    top: 16rem;
    left: 0rem;

}

img {

    position: relative;
    padding: 0.5rem;
    width: 38rem;
    height: 24.5625rem;
    border-radius: 0.75rem;
    border: 1px solid rgba(46, 51, 70, 0.16);
}